<script setup>

  import { onMounted, onUnmounted,reactive, ref, toRefs, provide } from 'vue';
  import { useLocaler, useLocale,useLang } from 'vue-localer';
  import VideoPlayer from '@/components/element/VideoPlayer.vue'
  import { Icon } from '@iconify/vue';
  import { useRoute } from 'vue-router'
  import { BigNumber, ethers } from "ethers";
  import { popup,toast,confirm} from '@/libraries/dialog.js'
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  import eventBus from '@/libraries/eventBus.js';
  import { getCurrentLang } from '@/core/localer.js';
  import axios from "axios";

  const { account,providerWeb3Modal } = mapState('wallet');
  const { connectWallet } = mapActions('wallet');
  const { setConfirms } = mapMutations('wallet');
  const { isUserConnected,getBalanceEth } = mapGetters('wallet');
  const { referrerId,referrerCount,referrerTotal,referrerReward,createReferrerStatus,rewardReferrerBoxStatus,rewardReferrerTokenStatus } = mapState('mystery');
  const { createReferrer,rewardReferrerBox,rewardReferrerToken,getReferrerInfo} = mapActions('mystery');
  const { apiHost,heroHost,analysis } = mapState('game');

  const referrerUrl =window.location.protocol + '//' + window.location.hostname  + (window.location.port.length>0?':' + window.location.port:'') +'/mystery/';
  const language = useLocale();
  const route = useRoute();


  eventBus.on("onConnected",async function(e){
    referral.init();
  });

  eventBus.on("onDisconnected",function(e){
    console.log("disconnected");
  });

  const referral = reactive({
    data:{
      pageIndex: 0,
      pageCount: 0,
      pageSize: 0,
      totalCount: 0,
      items: []
    },
    loading:false,
    pageIndex:0,
    init: async () =>{

      await referral.getReferrer();

      if (isUserConnected.value) {
        await getReferrerInfo();
      }
      
    },
    turnPage: (page)=>{
      referral.pageIndex = page;
      referral.getReferrer();
    },
    getReferrer: async()=>{
      referral.loading = true;

      let id = route.params.id;
      let url = apiHost.value+"/treasure/referrer?owner="+account.value+"&pageSize=10&pageIndex="+referral.pageIndex;
        axios.get(url)
        .then(r => {
          if(r.data && r.data.data && r.data.data.items.length>0){
            referral.data = r.data.data;
          }
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
          referral.loading = false;
        });
    },
    checkLink: async() =>{
      if(referrerId.value==0){
        popup('error',language.value.referral.link.noReferrerLink);
      }
    },
    createLink: async() =>{

      if(referrerId.value==0){
        if (!isUserConnected.value) {
          await connectWallet();
          return;
        } 
        await createReferrer();
      }else{
        referral.copy(referrerUrl + referral.base64Encode(referrerId.value));
        toast('success',language.value.referral.link.copied);
      }

    },
    claimToken: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      await rewardReferrerToken();
    },
    claimBox: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      await rewardReferrerBox();
    },
    currency: (value)=>{
      return (+value || 0).toFixed(2).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,');
    },
    getTimeAgo: (timestamp) => {
      
      let msPerMinute = 60 * 1000;
      let msPerHour = msPerMinute * 60;
      let msPerDay = msPerHour * 24;
      let msPerMonth = msPerDay * 30;
      let msPerYear = msPerDay * 365;
      let nowTime = Math.floor(Date.now());
      let elapsed = nowTime - timestamp*1000;
      if (elapsed < msPerMinute) 
      {
          return Math.round(elapsed/1000) + ' ' + language.value.common.time.seconds;   
      }else if (elapsed < msPerHour) 
      {
          return Math.round(elapsed/msPerMinute) + ' ' + language.value.common.time.minutes;   
      }else if (elapsed < msPerDay ) 
      {
          return Math.round(elapsed/msPerHour ) + ' ' + language.value.common.time.hours;   
      }else if (elapsed < msPerMonth) 
      {
          return Math.round(elapsed/msPerDay) + ' ' + language.value.common.time.days;   
      }else if (elapsed < msPerYear) 
      {
          return Math.round(elapsed/msPerMonth) + ' ' + language.value.common.time.months;   
      }else 
      {
          return Math.round(elapsed/msPerYear ) + ' ' + language.value.common.time.years;   
      }
    },
    copy: (data)=>{
        const textarea = document.createElement("textarea");
        textarea.textContent = data;
        textarea.setAttribute("style","position:absolute; right:200%;");
        document.body.appendChild(textarea);
        textarea.select();
        textarea.setSelectionRange(0, 99999);
        document.execCommand("copy");
        textarea.remove();
    },
    base64Encode: (data) =>{
      return window.encodeURI(window.btoa(data));
    },
    base64Decode: (data) =>{
      return window.decodeURI(window.atob(data));
    },
    onResize : (e) => {
      // if(window.innerWidth<640)
      // {
      //   referral.completed = true;
      // }
    },
    start: () =>{
      referral.init();
      window.addEventListener('resize', referral.onResize);
    },
    stop: () =>{
      window.removeEventListener('resize', referral.onResize);
    },
  });

  onMounted(() => {
    referral.start();
  });

  onUnmounted(()=>{
    referral.stop();
  });

</script>

